
body {
  margin: 0;
  font-family: Arial, sans-serif;
  transition: background-color 0.3s, color 0.3s;
}

[data-theme='dark'] {
  background-color: #111;
  color: #eee;
}

[data-theme='light'] {
  background-color: #fff;
  color: #111;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  background-color: #ff6f61;
  color: white;
}

.navbar-left,
.navbar-center,
.navbar-right {
  display: flex;
  align-items: center;
}

.navbar-center a {
  margin: 0 1rem;
  color: white;
  text-decoration: none;
  font-weight: bold;
}

.logo {
  height: 40px;
}

button {
  background: white;
  color: #ff6f61;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
}

main {
  padding: 2rem;
}

form {
  max-width: 400px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

form input,
form select {
  padding: 0.5rem;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
}

form span {
  color: red;
  font-size: 0.875rem;
}

/* Product Grid */
.product-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 1rem;
  margin-top: 2rem;
}

.product-card {
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 1rem;
  background-color: white;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.product-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
}

.product-card img {
  width: 100%;
  height: 150px;
  object-fit: contain;
}

/* Scroll Animation */
.fade-in {
  opacity: 0;
  transform: translateY(20px);
  animation: fadeInUp 0.6s forwards;
}

@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

label {
  display: flex;
  flex-direction: column;
  font-weight: bold;
}

input, select {
  margin-top: 4px;
}

.fade-in {
  animation: fadeInUp 0.5s ease-out forwards;
  opacity: 0;
}

h2 {
  text-align: center;
}

ul {
  list-style: none;
  padding: 0;
}

ul li {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1rem;
}

@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;600&display=swap');

body {
  font-family: 'Inter', sans-serif;
}

.theme-toggle {
  font-size: 1.5rem;
  background: none;
  border: none;
  cursor: pointer;
}

form {
  max-width: 500px;
  margin: 2rem auto;
  padding: 2rem;
  background: #f5f5f5;
  border-radius: 10px;
  box-shadow: 0 8px 16px rgba(0,0,0,0.1);
}

form h2 {
  margin-bottom: 1.5rem;
}

form label {
  font-weight: bold;
  margin-top: 1rem;
}

form input, form select {
  width: 100%;
  padding: 0.75rem;
  font-size: 1rem;
  margin-top: 0.25rem;
  margin-bottom: 0.75rem;
  border-radius: 6px;
  border: 1px solid #ccc;
}

form span {
  color: #ff3b3b;
  font-size: 0.875rem;
}

form button[type="submit"] {
  margin-top: 1rem;
  width: 100%;
  padding: 0.75rem;
  background: #ff6f61;
  border: none;
  color: white;
  font-weight: bold;
  border-radius: 6px;
  cursor: pointer;
  transition: background 0.3s ease;
}

form button[type="submit"]:hover {
  background: #e65b50;
}

.cart-container {
  max-width: 600px;
  margin: 2rem auto;
  background: #fff;
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 0 8px 16px rgba(0,0,0,0.1);
}

.cart-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.cart-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #eee;
  padding: 1rem 0;
}

.cart-info {
  flex-grow: 1;
  margin-left: 1rem;
}

.cart-total {
  font-weight: bold;
  margin-top: 1rem;
  text-align: right;
}

.checkout-button {
  display: block;
  margin-top: 1rem;
  background-color: #ff6f61;
  color: white;
  text-align: center;
  padding: 0.75rem;
  border-radius: 6px;
  text-decoration: none;
  font-weight: bold;
  transition: background 0.3s ease;
}

.checkout-button:hover {
  background-color: #e65b50;
}

/* Filter and search enhancements */
.filter-bar {
  display: flex;
  gap: 1rem;
  margin-bottom: 1rem;
  flex-wrap: wrap;
  justify-content: center;
}

.filter-bar input,
.filter-bar select {
  padding: 0.5rem 0.75rem;
  font-size: 1rem;
  border-radius: 6px;
  border: 1px solid #ccc;
}

.desc {
  font-size: 0.875rem;
  color: #555;
  margin-bottom: 0.5rem;
}

@media (max-width: 768px) {
  .cart-container,
  form {
    margin: 1rem;
    padding: 1rem;
  }

  .product-grid {
    grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  }

  .filter-bar {
    flex-direction: column;
    align-items: stretch;
  }

  .filter-bar input,
  .filter-bar select {
    width: 100%;
    margin-bottom: 0.5rem;
  }
}

.filter-bar input,
.filter-bar select {
  background-color: #fff5f4;
  border: 1px solid #ff6f61;
  border-radius: 8px;
  font-family: inherit;
  color: #333;
}

.filter-bar input:focus,
.filter-bar select:focus {
  outline: none;
  border-color: #ff6f61;
  box-shadow: 0 0 0 2px rgba(255, 111, 97, 0.2);
}

/* Navbar styles */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--navbar-bg, #fff);
  padding: 1rem 2rem;
  box-shadow: 0 4px 6px rgba(0,0,0,0.1);
  position: sticky;
  top: 0;
  z-index: 100;
}

.logo a {
  font-size: 1.5rem;
  font-weight: bold;
  text-decoration: none;
  color: var(--text-color, #333);
}

.menu {
  display: flex;
  gap: 1.5rem;
  align-items: center;
}

.menu a {
  text-decoration: none;
  color: var(--text-color, #333);
  font-weight: 500;
}

.theme-toggle {
  background: transparent;
  border: none;
  cursor: pointer;
  font-size: 1.1rem;
}

/* Light/Dark themes */
body.light {
  --navbar-bg: #ffffff;
  --text-color: #333333;
  background-color: #f9f9f9;
}

body.dark {
  --navbar-bg: #1a1a1a;
  --text-color: #f1f1f1;
  background-color: #121212;
}

/* Responsive navbar */
@media (max-width: 768px) {
  .navbar {
    flex-direction: column;
    align-items: flex-start;
    padding: 1rem;
  }

  .menu {
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    gap: 0.75rem;
    margin-top: 0.5rem;
  }

  .menu a, .theme-toggle {
    width: 100%;
    text-align: left;
  }
}
